.nav-bar-container{
  color: black;
  height: 100vh;
  min-width: 200px;
  transition: all 300ms linear;
}

.nav-bar-logo-container{
  margin: 10px 0 0 5px;
  position: fixed;
  cursor: pointer;
  transition: all 200ms ease;
}
.nav-bar-logo-container:hover{
  transform: scale(1.03, 1.03);
}

nav{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  position: fixed;
  width: 200px;
  height: 500px;
  background-color: #e0e0e0;
  border-radius: 0 6px 6px 0;
  border: 1px solid rgba(121, 120, 120, 0.452);
  top: 102px;
  left: -187px;
  padding-top: 20px;
}
.nav-section-container{
  margin: 10px 0;
  padding: 10px 0 20px 0;
}
nav h3{
  color: rgb(94, 93, 93); 
  font-weight: 300; 
  margin: 10px 0 33px 0;
  font-size: 20px;
}
nav a{
  font-weight: 600;
  font-size: 18px;
  display: block;
}
nav a:hover{
  color: #dd0c0cbb;
  border-left: 3px solid #dd0c0cbb;
}

    /* Animation parameters */

.nav-bar-resize{
  min-width: 60px;
}


.spinOut{
  animation: spinOut 300ms linear 1 reverse forwards;
  -webkit-animation: spinOut 300ms linear 1 reverse forwards;
  -moz-animation: spinOut 300ms linear 1 reverse forwards;
}
.spinIn{
  animation: spinIn 300ms linear 1 ;
  -webkit-animation: spinIn 300ms linear 1 ;
  -moz-animation: spinIn 300ms linear 1 ;
}

@keyframes spinOut {
  0%{transform: rotate(-55deg);}
  100%{transform: rotate(0deg);}
}
@-webkit-keyframes spinOut {
  0%{transform: rotate(-55deg);}
  100%{transform: rotate(0deg);}
}
@-moz-keyframes spinOut {
  0%{transform: rotate(-55deg);}
  100%{transform: rotate(0deg);}
}
@keyframes spinIn {
  0%{transform: rotate(-55deg);}
  100%{transform: rotate(0deg);}
}
@-webkit-keyframes spinIn {
  0%{transform: rotate(-55deg);}
  100%{transform: rotate(0deg);}
}
@-moz-keyframes spinIn {
  0%{transform: rotate(-55deg);}
  100%{transform: rotate(0deg);}
}


.navShow{
  animation: slideOut 300ms linear 1 forwards;
  -webkit-animation: slideOut 300ms linear 1 forwards;
  -moz-animation: slideOut 300ms linear 1 forwards;
}
.navHide{
  animation: slideIn 300ms linear 1;
  -webkit-animation: slideIn 300ms linear 1;
  -moz-animation: slideIn 300ms linear 1;
}

@keyframes slideOut{
  0%{left: -187px;}
  100%{left:0;}
}
@-webkit-keyframes slideOut{
  0%{left: -187px;}
  100%{left:0;}
}
@-moz-keyframes slideOut{
  0%{left: -187px;}
  100%{left:0;}
}
@keyframes slideIn{
  0%{left:0;}
  100%{left: -187px;}
}
@-webkit-keyframes slideIn{
  0%{left:0;}
  100%{left: -187px;}
}
@-moz-keyframes slideIn{
  0%{left:0;}
  100%{left: -187px;}
}
